<script>
  import {mapState, mapGetters} from 'vuex';
  import util from '../../js/util'
  import moment from 'moment'
//  import  loading from '../../plugins/loading/loading'
  import CascaderOrganize from "../../plugins/cascaderOrganize/index";
  import page from "../../plugins/page/index";
  import Choosen from '../biz/compositeChoosen.vue'
  export default {
    name: 'todoProcess',
    components: {
       page,
       Choosen
    },
    mounted() {
      var that = this;
      util.burialPoint('我的发起-进入我的发起页面');
      that.getList();
      window.onmessage = function (e) {   //监听页面传值
        var data = e.data;
        // 点击确认按钮的key
        if (data.key === 'refreshNewSystem') {
          that.getList();
        }
      };
    },
    data() {
      return {
        // 日期禁用  handtDate
        pickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.startDate[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        pickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.startDate[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        //结束时间 finshDate
        finshDatepickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.finshDate[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        finshDatepickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.finshDate[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        model: {
          selectForm: {
            'searchWord': '',//搜索
            'approveStatus': '',//流程状态
            'startDate': '', //开始时间
            'finishDate': '', //结束时间
            'amountOfMoneyB': '', //金额低
            'amountOfMoneyE': '', //金额高
            'processFullName': '',//流程类别
