    methods: {
      /**
       * 埋点事件
       */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      // 删除流程
      removeProcess(){
        this.showremove = true;
        util.burialPoint('我的发起-操作-删除流程按钮');
      },
      // 结束流程
      endProcess(){
        this.endsDialog=true;
        util.burialPoint('我的发起-操作-结束流程按钮');
      },
      // 点击input 不显示控件 点击icon图标显示控件
      hideDate(val) {
        util.burialPoint('我的发起-输入条件-发起时段');
        setTimeout( () => {
          let dateNodeStart = undefined;
          let dateNodeEnd = undefined;
          let startBool = null;
          let endBool = null;
          if (document.getElementsByClassName('el-picker-panel')[0]) {
            startBool = true;
          }
          if (document.getElementsByClassName('el-picker-panel')[1]) {
            endBool = true;
          }

          dateNodeStart = startBool ? document.getElementsByClassName('el-picker-panel')[0] : '';
          dateNodeEnd = endBool ? document.getElementsByClassName('el-picker-panel')[1] : '';

          if (val.$el.firstElementChild == document.activeElement) {
            startBool ? dateNodeStart.style.display = 'none' : '';
            endBool ? dateNodeEnd.style.display = 'none' : ''
          } else {
            startBool ? dateNodeStart.style.display = 'block' : '';
            endBool ? dateNodeEnd.style.display = 'block' : ''
          }
        }, 10)
      },
      //公司、组织、项目 控件 选择后触发
      changeSetvaule(val) {
        let data = val.data;
        this.model.selectForm.companyCode = (data.organize)?data.organize.orgID:''//公司 （ID）
        this.model.selectForm.departmentID = (data.department)?data.department.id:'' //部门（ID）
        this.model.selectForm.projectId = (data.projects)?data.projects.pc_ID:''  //项目（ID）
      },
      //替换表头的文字为图标
      renderIcon(create, {column}) {
        return create('i', [
          create("i",{
            "class":"iconfont icon-process"
          }),
        ]);
      },

      //表格排序
      sortChange(column){
        util.burialPoint('我的已办-列表-' + column.column.label + '排序');
        this.CriteriaModel.SortDirection  = `${column.order.replace('ending','')}`;
        this.CriteriaModel.SortField   = column.prop;
        this.getList();
      },

      /**
       * 打开弹出框组件
       */
      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        if(type=='processSelection'){//新流程选择
          util.burialPoint('我的发起-输入条件-流程类别');
          // 弹出框类型
          this.componentParam.componentType = type;
          //是否单选
          this.componentParam.oneCheck = true;
        }
        this.comp.component = type;
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },

      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
            /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        if (data.type && data.type == "processSelection") {   //选择流程类别
           this.componentParam.backfillData = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.model.selectForm.processFullName = processId.join(',');
          this.processFormName = processName.join('；');
        }
        this.hideComponent();
      },
      //重置查询条件
      refresh(){
        let that = this;
        that.CriteriaModel.ProcessNum='';
        that.serachSlectData.tagsOwnProcess = [];
        that.serachSlectData.tagsOwnCategory = [];
        that.CriteriaModel.ProcessCategory = [];
        that.CriteriaModel.Originator = '';
        that.CriteriaModel.DepartmentId = '';
        that.CriteriaModel.FormSubject = '';
        that.serachSlectData.tagsOrgSelect =[];
        that.serachSlectData.tagsOwnsUser = [];
        that.serachSlectData.ToWriteData = [];
        that.serachSlectData.CurrentUser = [];
        that.model.startDate = []; //发起时段
        that.model.handtDate = []; //发起时段
        that.model.finshDate = []; //发起时段
        that.CriteriaModel.ProcessStatus = that.state[0].value
      },
      //金额输入中验证
      inputValidate(position, value) {
        let that = this;
        let regPos = /^([0-9]*)([.]?|(\.\d{1,2})?)$/; //验证金额正则
        if (position == 'front') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.selectForm.amountOfMoneyB = '';
            }, 100);
          }
        } else if (position == 'back') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.selectForm.amountOfMoneyE = '';
            }, 100);
          }
        }
      },
      //金额输完验证
      checkValidate(position, value) {
        value = parseFloat(value);
        let that = this;
        if (position == 'front') {
          if (this.model.selectForm.amountOfMoneyE != '' && value > parseFloat(this.model.selectForm.amountOfMoneyE)) {
            that.model.selectForm.amountOfMoneyB = '';
          }
        } else if (position == 'back') {
          if (this.model.selectForm.amountOfMoneyB != '' && value < parseFloat(this.model.selectForm.amountOfMoneyB)) {
            that.model.selectForm.amountOfMoneyE = '';
          }
        }
      },

      /**
       * 短信催办 确定
       */
      correctinfo(){
        this.showMess =false;
        this.$axios.post('/api/myprocess/sendSortMessage',{procinstid:this.multipleSelection[0].procInstID}).then(res => {
          let data = eval('('+res.data.result+')');
           this.showNone =false;
          if(data.Status=='2'){
            this.$message({
              type: 'success',
              message: data.Message,
            });
            return;
          }
          if(data.Status=='1'){
            this.$message({
              type: 'warning',
              message: data.Message,
            });
            this.showMess =false;
            return;
          }
          if(data.Status=='3'){
            this.$message({
              type: 'warning',
              message: data.Message,
            });
            this.showMess =false;
            return;
          }
        }).catch((err)=>{
          this.$message.error(`发送失败！${err}`)
          this.showNone =false;
        })
        this.$refs.multipleTable.clearSelection();
      },

      /**
       * 短信催办 取消
       */
      cancelinfo(){
        this.showMess =false;
        //点击取消后取消当前选中的这一项
        this.$refs.multipleTable.clearSelection();
      },
      /**
       * 邮件催办 确定
       */
      correctmess(){
        this.showNone =false;
        this.$axios.post('/api/myprocess/sendEmai',{procinstid:this.multipleSelection[0].procInstID}).then(res => {
          let data = res.data.result;
          if(res.data.result.message === '邮件催办成功'){
            this.$message({type: 'success',message: '邮件催办成功!',});
          }else{
             this.$message({
              type: 'warning',
              message: res.data.result.message,
            });
          }
          if(res.data.result.hasOwnProperty('note')){
            this.$message.warning(`${data.note}`);
          }
        }).catch((err)=>{
          this.$message.error(`发送失败！${err}`)
        })
        //催办成功后取消当前选中的这一项
        this.$refs.multipleTable.clearSelection();
      },
       /**
       * 邮件催办 取消
       */
      cancelmess(){
        this.showNone =false;
        this.$refs.multipleTable.clearSelection();
      },
      /**
       * 回收 校验回收的流程是否是 审批中
       */
      clickRecovery(){;
        if(this.multipleSelection[0].status != 2) {
          this.$message.warning('只能回收审批中的流程！');
          //取消当前选中项
          this.$refs.multipleTable.clearSelection();
          return;
        }
        util.burialPoint('我的发起-操作-回收按钮');
        //保存 回收 的流程 procInstID
        this.currentId = this.multipleSelection[0].procInstID
        this.recoveryDialog = true;
      },
      mailUrging() {
        this.showNone=true;
        util.burialPoint('我的发起-操作-邮件催办按钮')
      },
      /**
       * 回收 确定
       */
      correctrecovery(){
        this.$axios.delete('/api/recyclingButton',{data: {"procInstID": this.multipleSelection[0].procInstID}}).then(res => {
          let data = res.data;
          if(data.messageList[0].key == "01") {
            for (let i = 0; i < this.tableData.length; i++) {
              //前端 删除 已经 回收过的流程。 注意：回收后台是不会立刻执行成功，所以不能刷新页面
              if (Number(this.tableData[i].procInstID) === Number(this.currentId)) {
                this.tableData.splice(i, 1);
                this.total=this.total-1;
                this.$message.success(`${data.messageList[0].message} 【回收请求已发出，10分钟后请再刷新确认】`);
              }
            }
          }else if(data.messageList[0].key == "04"){
            this.$message.warning(`回收失败：${data.messageList[0].message}`);
            this.$refs.multipleTable.clearSelection();//催办成功后取消当前选中的这一项
          }else{
             this.$message.error(`回收失败：${data.messageList[0].message}`);
          }
           this.recoveryDialog = false;
        }).catch((err)=>{
          this.$message.error(`回收失败！${err}`);
          this.$refs.multipleTable.clearSelection();
          this.recoveryDialog = false;
        })

      },
      /**
       * 回收 取消
       */
      cancelrecovery(){
        this.recoveryDialog =false;
        this.$refs.multipleTable.clearSelection();
      },
      /**
       * 删除流程 确定
       */
      correctremove(){
         var that = this;
         //输入去除空格
        this.textarea = this.textarea.replace(/\s+/g,"");
        if(this.textarea===''){
          this.$message.warning('请输入删除原因');
          return;
        }
        this.showremove = false;
        let applyReason=this.textarea;
        this.textarea='';

        this.$axios.post('api/requestDeleteProcessInstances',{procInstIds:that.multipleSelection[0].procInstID,applyReason}).then(res => {
          let data = eval('('+res.data.result+')');
          if(data.Status == '6') {
            //如果删除成功，再从新刷新列表
            that.$message.success(`${data.Message}`);
            that.getList();
          }else{
            that.$message.warning(`${data.Message}`);
            that.$refs.multipleTable.clearSelection();
          }
        }).catch((err)=>{
          this.$message.error(`删除失败！${err}`)
        })
      },
      /**
       * 删除流程 取消
       */
      cancelremove(){
        this.showremove =false;
        this.$refs.multipleTable.clearSelection();
      },
      /**
       * 结束流程 确定
       */
      correctend(){
        var urlParam = this.multipleSelection[0].viewUrl.substr(this.multipleSelection[0].viewUrl.indexOf('SN=') + 3, this.multipleSelection[0].viewUrl.length);
        urlParam = urlParam.substr(0, urlParam.indexOf('&'));
        this.$axios.post('/api/stopProcessInstances', {
          procInstId: this.multipleSelection[0].procInstID,
          sn: urlParam
        }).then(res => {
          let data = eval('(' + res.data.result + ')');
          if (data.Status === '4') {
            //如果删除成功，再从新刷新列表
            this.endsDialog = false;
            this.$message.success(`${data.Message}`);
            this.getList();
          } else {
            this.endsDialog = false;
            this.$message.warning(`${data.Message}`);
            this.$refs.multipleTable.clearSelection();

          }
        }).catch((err) => {
          this.endsDialog = false;
          this.$message.error(`结束流程失败！${err}`)
        })

      },
       /**
       * 结束流程 取消
       */
      cancelend(){
        this.endsDialog =false;
        //点击取消后取消当前选中的这一项
        this.$refs.multipleTable.clearSelection();
      },
      //点击搜索,通过所添加的条件查询到信息并显示到当前列表
      search() {
        util.burialPoint('我的发起-输入条件-搜索按钮');
        this.pageIndex = 1;
        this.getList();
      },

      //获取列表
      getList() {
        var that = this;
        //开始时间
        that.model.startDate == null ? (that.model.startDate = []) : "";

        //结束时间
        that.model.finshDate == null ? (that.model.finshDate = []) : "";

        that.CriteriaModel.SubmitStartDate  = that.model.startDate[0]? `${that.model.startDate[0]} 00:00:00`: "";
        that.CriteriaModel.SubmitEndDate  = that.model.startDate[1]? `${that.model.startDate[1]} 23:59:59`: "";

        that.CriteriaModel.FinishStartDate  = that.model.finshDate[0]? `${that.model.finshDate[0]} 00:00:00`: "";
        that.CriteriaModel.FinishEndDate  = that.model.finshDate[1]? `${that.model.finshDate[1]} 23:59:59`: "";

        that.CriteriaModel.ProcessNum = that.CriteriaModel.ProcessNum.replace(/\s+/g,"");
        that.CriteriaModel.FormSubject = that.CriteriaModel.FormSubject.replace(/\s+/g,"");
        //填单人 SubmitterAccount
        if(that.serachSlectData.ToWriteData.length>0){
          that.CriteriaModel.SubmitterAccount = that.serachSlectData.ToWriteData[0].UserAccount;
        }else {
          that.CriteriaModel.SubmitterAccount = ''
        }
        //处理人
        if(that.serachSlectData.CurrentUser.length>0){
          that.CriteriaModel.CurrentUser =that.serachSlectData.CurrentUser[0].UserAccount;
        }else {
          that.CriteriaModel.CurrentUser = ''
        }
        //申请人
        if(that.serachSlectData.tagsOwnsUser.length>0){
          that.CriteriaModel.Originator = that.serachSlectData.tagsOwnsUser[0].UserAccount;
        }else {
          that.CriteriaModel.Originator = ''
        }
        //部门
        if( that.serachSlectData.tagsOrgSelect.length>0){
          that.CriteriaModel.DepartmentId = that.serachSlectData.tagsOrgSelect[0].UserAccount;
        }else {
          this.CriteriaModel.DepartmentId = '';
        }
//        //流程
        if( that.serachSlectData.tagsOwnProcess){
          that.CriteriaModel.ProcessCode = [];
          for(let item of this.serachSlectData.tagsOwnProcess){
            that.CriteriaModel.ProcessCode.push(item.processCode);
          }
        }
        //流程类别
        if(that.serachSlectData.tagsOwnCategory){
          that.CriteriaModel.ProcessCategory = [];
          for(let v of that.serachSlectData.tagsOwnCategory){
            that.CriteriaModel.ProcessCategory.push(v.id)
          }
        }

        that.imgLoading = true;
        // 分页信息
        that.$axios({
          url:'/Portal/WorkFlow/GetMyApplications',
          method:'post',
          data:this.CriteriaModel
        }).then(result => {
          if(result.data.code === 200) {
            that.tableData = result.data.data.item;
            for(let item of  that.tableData){
              item.FinishDate =  item.FinishDate ? moment(item.FinishDate).format('YYYY-MM-DD HH:mm') : '';
              if(item.ApprovalUsers){
                item.ApprovalUsers = item.ApprovalUsers.split(",");
              }
            }
            that.CriteriaModel.total = result.data.data.total;
          }else {
            that.$message.error("请求失败！");

          }
          // loading.endLoading()
          that.imgLoading = false;
        }).catch(err => {
          // loading.endLoading()
          that.imgLoading = false;
          that.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        });
      },

      /**
       * 页面size变更方法
       */
      handleSizeChange(val) {
        util.burialPoint('我的发起-列表-每页显示条数按钮');
        var pageSize = `${val}`;
        this.CriteriaModel.PageIndex = 1;
        this.CriteriaModel.PageSize = parseInt(pageSize);
        localStorage.myInitPageSize = val;
        this.getList();
      },
      /**
       * 翻页方法
       */
      handleCurrentChange(val) {
        util.burialPoint('我的发起-列表-页码按钮');
        this.CriteriaModel.PageIndex = val;
        this.getList();
      },

