      },

      //全选
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      //流程单号跳转
      choosenDialogType(val,type){
        var that = this;
        that.typeTab = val;
        //打开流程弹窗
        that.ProcessType = type;
        that.$refs.dialogChoosen.openChoosen(that.typeTab,type);
        that.$refs.dialogChoosen.getCollect();
      },
      //打开弹窗
      OpenDialog(val){
        var that = this;
        that.Visible = val;
      },
      //关闭弹窗
      CloseDialog(val){
        var that = this;
        that.Visible = val;
      },
      //接收子组件确定按钮带过的数据
      sendTag(val,type){
        let _this = this;
        if(val.length > 0) {
          if (type) {
            for (let item of val) {
              if (type === 'CurrentUser') {
                _this.serachSlectData.CurrentUser = [];
                if (_this.serachSlectData.CurrentUser.length > 0) {
                  for (let i of _this.serachSlectData.CurrentUser) {
                    if (i.SysId === item.SysId) {
                      return
                    } else {
                      _this.serachSlectData.CurrentUser.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.CurrentUser.push(item);
                }
              }
              else if (type === 'User') {
                _this.serachSlectData.tagsOwnsUser = [];
                if (_this.serachSlectData.tagsOwnsUser.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnsUser) {
                    if (i.SysId === item.SysId) {
                      return
                    } else {
                      _this.serachSlectData.tagsOwnsUser.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnsUser.push(item);
                }
              }
              else if (type === 'Process') {
                if (_this.serachSlectData.tagsOwnProcess.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnProcess) {
                    debugger
                    if (i.id === item.id) {
                      return
                    } else {
                      _this.serachSlectData.tagsOwnProcess.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnProcess.push(item);
                }
              }
              else if (type === 'Category') {
                if (_this.serachSlectData.tagsOwnCategory.length > 0) {
                  for (let i of _this.serachSlectData.tagsOwnCategory) {
                    if (i.id === item.id) {
                      return
                    } else {
                      _this.serachSlectData.tagsOwnCategory.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOwnCategory.push(item);
                }
              }
              else if (type === 'ToWrite') {
                if (_this.serachSlectData.ToWriteData.length > 0) {
                  for (let i of _this.serachSlectData.ToWriteData) {
                    if (i.id === item.id) {
                      return
                    } else {
                      _this.serachSlectData.ToWriteData.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.ToWriteData.push(item);
                }
              }
              else if (type === 'Org') {
                _this.serachSlectData.tagsOrgSelect = [];
                if (_this.serachSlectData.tagsOrgSelect.length > 0) {
                  for (let i of _this.serachSlectData.tagsOrgSelect) {
                    if (i.SysId === item.SysId) {
                      return
                    } else {
                      _this.serachSlectData.tagsOrgSelect.push(item);
                    }
                  }
                }
                else {
                  _this.serachSlectData.tagsOrgSelect.push(item);
                }
              }
            }
          }
        }else {
          switch (type){
            case 'Process':{
              _this.serachSlectData.tagsOwnProcess = [];
              break
            }
            case 'Category':{
              _this.serachSlectData.tagsOwnCategory = [];
              break;
            }
            case 'User':{
              _this.serachSlectData.tagsOwnsUser = [];
              break;
            }
            case 'Org':{
              _this.serachSlectData.tagsOrgSelect = [];
              break
            }
            case 'CurrentUser':{
              _this.serachSlectData.CurrentUser = [];
              break
            }
          }
        }
      },
      //删除页面回显中的tag
      closeProcessTag(item){
        if(item.portalType){
          switch (item.portalType){
            case 'CurrentUser':{
              let index = this.serachSlectData.CurrentUser.indexOf(item);
              this.CriteriaModel.CurrentUser = '';
              this.serachSlectData.CurrentUser.splice(index,1);
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.CurrentUser,null);
              break
            }
            case 'Process':{
              let index =  this.serachSlectData.tagsOwnProcess.indexOf(item);
              this.serachSlectData.tagsOwnProcess.splice(index,1);
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnProcess);
              break;
            }
            case 'Category':{
              let index =  this.serachSlectData.tagsOwnCategory.indexOf(item);
              this.serachSlectData.tagsOwnCategory.splice(index,1);
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnCategory);
              break;
            }
            case 'User':{
              let index = this.serachSlectData.tagsOwnsUser.indexOf(item);
              this.serachSlectData.tagsOwnsUser.splice(index,1);
              this.CriteriaModel.Originator = '';
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnsUser,null);
              break
            }
            case 'Org':{
              let index = this.serachSlectData.tagsOrgSelect.indexOf(item);
              this.serachSlectData.tagsOrgSelect.splice(index,1);
              this.CriteriaModel.DepartmentId = '';
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOrgSelect,null);
              break
            }
            case 'ToWrite':{
              let index = this.serachSlectData.ToWriteData.indexOf(item);
              this.serachSlectData.ToWriteData.splice(index,1);
              this.CriteriaModel.SubmitterAccount = '';
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.ToWriteData,null);
              break
            }
          }
        }else {
          switch (item.type){
            case 'Process':{
              let index =  this.serachSlectData.tagsOwnProcess.indexOf(item);
              this.serachSlectData.tagsOwnProcess.splice(index,1);
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnProcess);
              break;
            }
            case 'Category':{
              let index =  this.serachSlectData.tagsOwnCategory.indexOf(item);
              this.serachSlectData.tagsOwnCategory.splice(index,1);
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnCategory);
              break;
            }
            case 'User':{
              let index = this.serachSlectData.tagsOwnsUser.indexOf(item);
              this.serachSlectData.tagsOwnsUser.splice(index,1);
              this.CriteriaModel.Originator = '';
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnsUser,null);
              break
            }
            case 'Org':{
              let index = this.serachSlectData.tagsOrgSelect.indexOf(item);
              this.serachSlectData.tagsOrgSelect.splice(index,1);
              this.CriteriaModel.DepartmentId = '';
              this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOrgSelect,null);
              break
            }
          }
        }
      },
      /**
       * 点击主题跳转到旧界面
       */
      jumpToView(row) {
        window.open(row.FlowViewUrl,'_blank');
        var mychar = document.getElementById('dailog_mask');
        mychar.style.display = 'none';
      },
      folioBlank(row){
        window.open(row.Url,'_blank');
      },
      //初始设置是否勾选
      checkBoxForm(row,index){
        if(row.IsBatchApproval){
          return 1;
        }else{
          return 0;
        }
      },
    },
    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight;
      },
      ...mapGetters(['isIE','isSafari', 'bodyHeight'])
    }
  };
</script>

<style lang="scss" scoped>
  .list{
    a{
      cursor: pointer;
    }
    a:hover{
      text-decoration: underline;
      color: #00a1f1;
    }
  }
</style>




